nav {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 75px;
  padding-top: 15px;
}
.container {
  width: 800px;
  margin: 0 auto;
  background-color: var(--background-color);
}
.links {
  background-color: var(--font-color);
  font-family: var(--font-family);
  color: var(--background-color) /* #fff*/;
  padding: 10px;
  border: none;
  text-align: center;
  text-decoration: none;
  font-size: 13px;
}
.button {
  border-radius: 36px;
  padding: 10px 20px;
  margin: 5px;
  cursor: pointer;
}
.links:visited {
  background-color: var(--font-color);
  text-decoration: none;
}
.links:hover {
  background-color: var(--subheading-color);
  text-decoration: none;
  /* background-color: rgb(140, 136, 115); */
}
.links:active {
  background-color: black;
  text-decoration: none;
}
